* {
  font-family: "Open Sans", sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  background-color: #0f0f0f;
  font-size: 16px;
}

.App .background {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -10;
}
.App .background img {
  height: 100%;
  position: absolute;
  right: 0;
  bottom: 0;
}/*# sourceMappingURL=App.css.map */